a, .btn {
    @include cubic-transition;
}

.btn {
    border-width: 2px;
    border-radius: 10px;
    font-size: rem-calc(20);
    font-weight: 700;
    padding: 15px 15px;
    &:focus { 
        outline: 0px; box-shadow: none; 
    }
}

.btn-primary {
    border-color: $primary-dark;
    box-shadow: 5px 7px 0 $primary-dark;
    &.bg-color1 {
        color: $primary;
    }
    &:hover {
        color: #F7E8F3;
        background: $primary-dark !important; 
    }
}

.btn-outline-primary {
    box-shadow: 5px 7px 0 $primary;
}

.btn-primary-light {
    color: $white;
    border-color: $primary;
    box-shadow: 5px 7px 0 $primary;
    &:hover {
        color: $white;
        border-color: $primary;
        background: $primary;
    }
}

.btn-sm {
    font-size: 18px;
    line-height: 28px;
    padding: 11px 15px;
}

.btn-xl {
    font-size: rem-calc(24);
    padding: 15px 22px;
}

.toggle-btn {
    font-size: rem-calc(22);
    display: inline-flex;
    align-items: center;
    line-height: 1;
    span {
        border-bottom: 1px solid $primary;
        color: $primary;
        @include cubic-transition;
    }
    .icon {
        margin-left: 9px;
        margin-top: -9px;
        @include cubic-transition;
    }
}

@media(max-width:767px) {
    .btn {
        padding: 11px 15px;
        font-size: rem-calc(18);
        line-height: 28px;
    }
    .btn-xl {
        font-size: rem-calc(20);
        padding: 14px 17px;
    }
}