

/*
*slideshow2-nav
*/
.slideshow2  {
    .slick-list{
        overflow: visible;
    }
    .slick-slide {
        img {
            filter: drop-shadow(10px 14px 20px rgba($primary, .3));
            animation: bounce 5s ease-in-out infinite;;
        }
    }
}
.slideshow2-nav {
    .thumb-img {
        position: relative;
        cursor: pointer;
        width: 84px;
        height: 76px;
        border-radius: 10px;
        border: 2px solid #8A5FA1;
        box-shadow: 5px 7px 0 #8A5FA1;
        overflow: hidden;
        display: block;
        &:before {
            position: absolute;
            content: '';
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            background: rgba($black, .2 );
            opacity: 0;
            @include cubic-transition;
        }
        img {
           width: 100%;
            height: 100%;
            display: block;
            object-fit: cover;
        }   
        &:hover {
            &:before {
                opacity: 1;
            }
        }
    }
    .slick-slide {
        padding-bottom: 36px;
    }
    .slick-current {
        .thumb-img {
            &:before {
                opacity: 1;
            }
        }
    }
}

@media (max-width: 991px) {
    .product-image-slider {
        .slideshow2 {
            margin-bottom: 30px;
            text-align: center;
            img {
                display: inline-block;
            }
        }
        .slideshow2-nav  {
            .slick-slide {
                padding: 0 10px 36px;
            }
        }
    }
}

@media (min-width: 992px) {
    .product-image-slider {
        position: relative;
        padding-right: 133px;
        min-height: 372px;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        .slideshow2 {
           width: 100%;
        }
        .slideshow2-nav {
            position: absolute;
            right: 20px;
            top: 50%;
            transform: translateY(-50%);
            width: 91px !important;
           
        }
    }
}

@keyframes bounce {
    0% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(3%);
    }
    100% {
        transform: translateY(0);
    }
}