/*
*box
*/
.box1 {
    background: $white;
    border-radius: 10px;
    border: 2px solid $primary;
    padding: 22px 38px 39px;
    height: 100%;
    display: flex;
    flex-direction: column;
    .figure1 {
        margin-bottom: 20px;
        img {
            border-radius: 10px;
        }
    }
    h2 {
        font-size: rem-calc(32);
        font-family: $font-family-tertiary;
        font-weight: 400;
        margin-bottom: 10px;
    }
    h6 {
        font-size: rem-calc(22);
        font-weight: 700;
    }
    p {
        font-size: rem-calc(22);
        line-height: calc(26 / 22);
        margin-bottom: 7px;
    }
    .timings-chart {
        margin-bottom: 30px;
        .custom-table1 {
            display: none;
        }
        &.open {
            .toggle-btn {
                span {
                    border-color: transparent;
                }
                .icon {
                    transform: rotate(-180deg);
                    margin-top: 3px;
                }
            }
            .custom-table1 {
                display: block;
            }
        }
        .toggle-btn {
            margin-bottom: 8px;
        }
    }
    footer {
        margin-top: auto;
    }
}

@media (max-width: 1199px) {
    .box1 {
        padding: 25px 15px 25px;
        p {
            font-size: rem-calc(18);
        }
    }
}

@media (max-width: 991px) {
    .box1 {
       h2 {
        font-size: rem-calc(28);
       }
    }
}

@media (max-width: 767px) {
    .box1 {
        .column {
            margin-bottom: 4px;
        }
    }
}