.gallery1 {
    border-radius: 10px;
    overflow: hidden;
    position: relative;
    .animation {
        width: 100%;
        top: 0;
        left: 0;
        clip-path: polygon(0% 0%, 0% 0%, 0% 100%, 0% 100%);
        object-fit: cover;
        position: absolute;
    }
    .completed {
        clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
    }
    .first {
        clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
    }
    .active {
        clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
        z-index: 2;
        @include cubic-transition;
        transition-duration: 2s;
    }
  }