/*
*list
*/
.list1 {
    padding: 0;
    list-style-type: none;
    margin-bottom: 24px;
    li {
        font-size: rem-calc(22);
        line-height: calc(28 / 22);
        color: #8D8D8D;
    }
}

@media (max-width: 1199px) {
    .list1  {
        margin-bottom: 20px;
        li {
            font-size: rem-calc(18);
        }
    }
}

/*
*list2
*/
.list2 {
    padding: 0;
    list-style-type: none;
    margin-bottom: 40px ;
    li {
        font-size: rem-calc(22);
        line-height: calc(30 / 22);
        color: $white;
        margin-bottom: 26px;
        position: relative;
        padding-left: 40px;
        &:before {
            content: '';
            width: 26px;
            height: 16px;
            background: url('../images/cloud-list.svg') no-repeat;
            position: absolute;
            top: 7px;
            left: 0;
        }
    }
}

@media (max-width: 767px) {
    .list2 {
        li {
            font-size: rem-calc(18);
        }
    }
}

/*
*desc-list
*/
.list3 {
    padding: 0;
    margin-left: 0;
    li {
        display: flex;
        flex-wrap: wrap;
        border-bottom: 2px dashed #8A5FA1;
        padding: 24px 0 22px;
        span {
            width: 50%;
            color: #8A5FA1;
            font-family: $font-family-secondary;
            font-weight: 400;
            line-height: 1.1;
            + span {
                padding-left: 15px;
            }
        }
        .value {
            text-align: right;
            font-weight: 700;
        }
    }
}

@media (max-width: 767px) {
    .list3 {
        li {
            font-size: rem-calc(16);
            padding: 18px 0 16px;
        }
    }
}