/*
*form
*/
.form {
    .form-group {
        margin-bottom: 34px;
    }
    .form-control {
        border: 2px solid $primary;
        border-radius: 10px;
        font-size: rem-calc(22);
        line-height: 30px;
        font-weight: 400;
        color: #9A9A9A;
        padding: 15px 25px;
        box-shadow: 5px 7px 0 #672567;
    }
    .form-control::-webkit-input-placeholder { 
        color: #9A9A9A;
      }
      
      .form-control:-ms-input-placeholder {
        color: #9A9A9A;
      }
      
      .form-control::placeholder {
        color: #9A9A9A;
      }
      textarea {
        resize: none;
        min-height: 196px;
      }
      .calendar {
        background-image: url('../images/calendar-icon.svg');
        background-repeat: no-repeat;
        background-size: 22px 22px;
        background-position: center right 25px;
        padding-right: 55px;
      }
}

@media (max-width: 767px) {
    .form {
        .form-control {
            font-size: rem-calc(18);
            padding: 10px 22px;
        }
    }
}


/*
*newsletter form
*/
.email-form {
    form {
        display: flex;
    }
    .btn {
        min-width: 120px;
        flex-shrink: 0;
        margin-left: 22px;
        
    }
}

@media (max-width: 767px) {
    .newsletter-form {
        .btn {
            min-width: auto;
        }
    }
}

/*
*file-upload
*/
.file-upload {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    position: relative;
    .btn {
        flex-shrink: 0;
        margin-right: 27px;
    }
    .file-name {
        font-size: rem-calc(24);
        color: $primary;
        flex: 1;
    }
    input {
        position: absolute;
        opacity: 0;
        visibility: hidden;
    }
}

@media (max-width: 767px) {
    .file-upload {
        .file-name {
            font-size: rem-calc(14);
        }
        .btn {
            margin-right: 12px;
        }
    }
}
