/*
*sociables
*/
.sociables {
    padding: 0;
    list-style-type: none;
    li {
        display: inline-block;
        + li {
            margin-left: 12px;
        }
    }
    &.alt {
        max-width: 212px;
        min-height: 210px;
        position: relative;
        li {
            margin-left: 0;
        }
        a {
            display: inline-block;
            &:hover {
                transform: scale(1.1) rotate(5deg);
            }
        }
        .twitter {
            position: relative;
            top: 27px;
            right: -22px;
        }
        .insta {
            position: absolute;
            top: 106px;
            left: 27px;
        }
        .tiktok {
            position: absolute;
            bottom: 0;
            right: 0;
        }
    }
}