/*
*carouse1
*/
.carousel1 {
    display: flex;
    flex-wrap: wrap;
    .figure1 {
        img {
            border-radius: 10px;
        }
    }
    > .item {
        width: calc(16.666% - 24px);
        + .item {
            margin-left: 24px;
        }
    }
   > .item:nth-child(odd), .owl-stage .owl-item:nth-child(odd) .item {
        .figure1 {
            transform: rotate(5deg);
        }
    }
   > .item:nth-child(even), .owl-stage .owl-item:nth-child(even) .item {
        .figure1 {
            transform: rotate(-5deg);
        }
    }
    .owl-stage-outer {
        overflow: unset;
    }
}

/*
*carousel3
*/
.carousel2 {
    .owl-stage-outer {
        overflow: initial;
    }
    .owl-stage {
        display: flex;
        flex-wrap: wrap;
        .item {
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 5px;
            img {
                max-height: 36px;
            }
        }
    }
}