/*
*container
*/
#container {
    min-width: 320px;
}

html.skrollr-mobile {
    overflow: initial !important;
}

/*
*header
*/
#header {
    padding: 20px 0;
    position: relative;
    z-index: 201;
}

@media (max-width: 767px) {
    #header {
        .logo {
            img {
                max-height: 50px;
            }
        }
    }
}

/*
*user-options
*/
#user-options {
    padding: 0;
    list-style-type: none;
    padding-right: 10px;
    li {
        display: inline-block;
        margin-right: 24px;
        font-size: rem-calc(18);
        font-weight: 300;
        &:last-child {
            margin-right: 0;
        }
    }
    .cart {
        position: relative;
        .total {
            font-size: rem-calc(23);
            font-weight: 300;
            position: absolute;
            top: -16px;
            right: -14px;
        }
    }
}

@media (max-width: 767px) {
    #user-options {
        padding-right: 0;
        margin-left: -4px;
        li {
            font-size: rem-calc(12);
            margin-right: 3px;
            svg {
                max-height: 20px;
                width: auto;
            }
        }
        .cart {
            .total {
                font-size: rem-calc(12);
                top: -13px;
                right: -4px;
            }
        }
    }
}


.open-menu {
    #user-options {
        li {
            a {
                color: $white;
            }
            svg {
                path {
                    fill: $white;
                }
            }
            .cart {
                .total {
                    color: $white;
                }
            }
        }
    }
}

/*
* page head
*/
#page-head {
    padding: 96px 0 58px;
    position: relative;
    overflow: hidden;
    overflow-x: hidden;
    .container {
        position: relative;
        z-index: 10;
    }
    p {
        font-family: $font-family-tertiary;
        font-weight: 400;
        font-size: rem-calc(40);
        line-height: 1;
        margin-bottom: 53px;
    }
    .cloud1 {
        top: 134px;
        //left: 48px;
        animation-delay: -160.69230769s;
        animation-duration: 88s;  
        background-size: 137px 84px;
    }
    .cloud2 {
        bottom: 50px;
        height: 60px;
        //left: 102px;
        animation-delay: -158.69230769s;
        animation-duration: 88s;
        background-size: 99px 60px;
    }
    .cloud3 {
        top: 124px;
        height: 113px;
        animation-delay: -18.46153846s;
        animation-duration: 210s;
        background-size: 186px 113px;
    }
    .cloud4 {
        bottom: 122px;
        height: 60px;
        //right: 214px;
        animation-delay: -22.46153846s;
        animation-duration: 200s;
        background-size: 99px 60px;
    }
    .cloud5 {
        top: 300px;
        //left: 48px;
        animation-delay: -130.69230769s;
        animation-duration: 77s;  
        background-size: 137px 84px;
    }
    .cloud6 {
        bottom: 100px;
        height: 60px;
        //left: 102px;
        animation-delay: -50.69230769s;
        animation-duration: 88s;
        background-size: 99px 60px;
    }
    .cloud7 {
        top: 50%;
        margin-top: -60px;
        height: 113px;
        animation-delay: -50.46153846s;
        animation-duration: 110s;
        background-size: 186px 113px;
    }
    .cloud8 {
        bottom: 22px;
        height: 60px;
        //right: 214px;
        animation-delay: -40.46153846s;
        animation-duration: 160s;
        background-size: 99px 60px;
    }
    &.page-head2 {
        h1 {
            line-height: 1;
        }
        p { 
            font-size: rem-calc(24);
            line-height: calc(32 / 24);
            font-family: $font-family-sans-serif;
            font-weight: 300;
        }
    }
}

@media (max-width: 991px) {
    #page-head {
        p {
            font-size: rem-calc(30);
        }
    }
}

@media (max-width: 767px) {
    #page-head {
        p {
            font-size: 22px;
            margin-bottom: 30px;
        }
        .cloud {
            img {
                max-height: 50px;
            }
        }
        .cloud1 {
            top: 10px;
            height: 40px;
            background-size: 70px 40px;
        }
        .cloud2 {
            bottom: 10px;
            height: 30px;
            background-size: 50px 30px;
        }
        .cloud3 {
            top: 40px;
            height: 30px;
            background-size: 50px 30px;
        }
        .cloud4 {
            bottom: 20px;
            height: 40px;
            background-size: 70px 40px;
        }
        .cloud5 {
            height: 40px;
            background-size: 70px 40px;
            top: 50%;
        }
        .cloud6 {
            height: 30px;
            background-size: 50px 30px;
        }
        .cloud7 {
            height: 25px;
            background-size: 45px 25px;
        }
        .cloud8 {
            height: 40px;
            background-size: 70px 40px;
            top: 30%;
        }
    }
}

.menu-btn {
    width: 84px;
    height: 137px;
    background: url('../images/menu-bg.png') no-repeat;
    background-size: 100% 100%;
    color: $white;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: -13px;
    top:-12px;
    font-weight: 300;
    color: #F7E8F3;
    font-size: rem-calc(20);
    @include cubic-transition;
    span {
        writing-mode: vertical-rl;
        transform: rotate(-180deg);
    }
    .title:before {
        content: 'menu';
        position: static;
    }
    .title:after {
        content: 'close';
        opacity: 0;
        position: absolute;
    }
    &:hover {
        color: $white;
        left: -8px;
    }
}

.open-menu {
    .menu-btn {
        .title {
            &::before {
                opacity: 0;
                position: absolute;
            }
            &:after {
                opacity: 1;
                position: static;
            }
        }
    }
}

@media (max-width: 767px) {
    .menu-btn {
        width: 50px;
        height: 101px;
        font-size: rem-calc(15);
    }
}

.note {
    border-width: 2px;
    border-radius: 10px;
    font-size: rem-calc(20);
    line-height: calc(30 / 20);
    font-weight: 700;
    padding: 15px 15px;
    color: $primary;
    border-radius: 50px;
    border: 2px solid $primary;
    box-shadow: 5px 7px 0 $primary;
    display: inline-block;
}

@media (min-width: 768px) {
    .note {
        min-width: 500px;
    }
}

/*
*main navigation
*/
html.open-menu {
    overflow: hidden !important;
    body {
        overflow: hidden !important;
    }
    #container {
        overflow: hidden !important;
    }
    #main-navigation {
        top: 0;
        h2 {
            margin-top: 0;
            opacity: 1;
            transition-delay: 1s;
            transition-duration: 1s;
        }
        .menu {
            li {
                margin-top: 0px;
                opacity: 1;
                transition-delay: 1.1s;
                transition-duration: 1.1s;
            }
        }
        .menu-images {
            opacity: 1;
            transition-delay: 1s;
            transition-duration: 1s;
        }
    }
}

#main-navigation {
    position: fixed;
    width: 100%;
    height: 100%;
    background: $primary-light;
    z-index: 200;
    padding-top: 124px;
    top: -100%;
    transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1) 0s;
    transition-delay: 0.5s;
    transition-duration: 1s;
    h2 {
        color: $white;
        margin-bottom: 50px;
        margin-top: -25px;
        opacity: 0;
        transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1) 0s;
        transition-delay: 0.3s;
        transition-duration: 0.3s;
    }
    .scrollable-content {
        overflow-x: auto;
        height: 100%;
        padding: 76px 0 52px;
    }
    .menu {
        li {
            margin-top: -15px;
            opacity: 0;
            transition-delay: 0.2s;
            transition-duration: 0.2s;
        }
    }
    .menu {
        padding: 0px;
        list-style-type: none;
        li {
            font-size: rem-calc(32);
            line-height: calc(40 / 32);
            color: $white;
            font-weight: 400;
            a {
                color: inherit;
                position: relative;
                &:after {
                    position: absolute;
                    content: '';
                    width: 0;
                    height: 1px;
                    bottom: 0px;
                    background: $primary;
                    left: 0;
                    @include cubic-transition;
                }
                &:hover {
                    color: $primary;
                    &:after {
                        width: 100%;
                    }
                }
            }
        }
    }
    .menu-images {
        margin-left: -50px;
        opacity: 0;
        transition-delay: 0.3s;
        transition-duration: 0.3s;
        .image {
            animation: fadeOut 2s;
            &.active {
                display: block;
                animation: fadeIn 2s;
            }
        }
    }
}

@media(max-width: 991px) {
    #main-navigation {
        h2 {
            margin-bottom: 30px;
        }
        .menu {
            li {
                font-size: rem-calc(24);
            }
        }
    }
}

@media (max-width: 767px) {
    #main-navigation {
        padding-top: 90px;
        .menu-images {
            margin-left: 0;
        }
    }
}

/*
*footer
*/
#footer {
    background: #F7E8F3;
    padding: 70px 0 12px;
    .logos {
        margin-bottom: 71px;
        max-width: 304px;
        a {
            display: inline-block;
            width: calc(50% - 14px);
            + a {
                margin-left: 22px;
            }
        }
    }
    h6 {
        font-size: rem-calc(16);
        font-weight: 700;
        text-transform: uppercase;
        margin-bottom: 21px;
    }
    .footer-navigation {
        padding: 0;
        list-style-type: none;
        li {
            font-size: rem-calc(16);
            line-height: 1;
            font-weight: 300;
            margin-bottom: 12px;
            a {
                color: $primary;
                &:hover {
                    color: $primary-dark;
                }
            }
        }
    }
    p {
        font-size: rem-calc(14);
        color: $primary;
        font-weight: 300;
    }
    .sociables {
        margin-left: auto;
    }
    .policies {
        padding: 0;
        margin-left: 0;
        display: flex;
        flex-wrap: wrap;
        list-style-type: none;
        li {
            font-size: rem-calc(14);
            font-weight: 300;
            + li {
                margin-left: 36px;
            }
            a {
                color: $primary;
            }
        }
    }
    h5 {
        font-size: rem-calc(32);
        font-weight: 400;
        line-height: calc(34 / 32);
        color: $primary;
        font-family: $font-family-tertiary;
        margin-bottom: 18px;
    }
    footer {
        padding-top: 32px;
    }
    .email-form {
        .form-control {
            font-size: rem-calc(16);
            line-height: 24px;
            padding: 10px 20px;
        }
        .btn {
            min-width: 120px;
            font-size: rem-calc(16);
        }
    }
}

@media (max-width: 1199px) {
    #footer {
        h5 {
            font-size: rem-calc(20);
            line-height: 1;
        }
        .newsletter-form .btn {
            margin-left: 10px;
        }
    }
}

@media (max-width: 991px) {
    #footer {
        .logos {
            margin: 0 auto 40px;
        }
        .policies li + li {
            margin-left: 20px;
        }
    }
}

@media (max-width: 767px) {
    #footer {
        padding: 50px 0 34px;
        text-align: center;
        h6 {
            margin-bottom: 16px;
        }
        .sociables {
            margin: 0 auto 16px;
        }
        footer {
            text-align: center;
            padding-top: 0;
            .policies {
                justify-content: center;
            }
        }    
    }
}
