body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: auto !important;
  height: auto !important;
}


.group {
  @include clearfix();
}

img {
  max-width: 100%;
}

figure {
  margin: 0px;
}

h1 {
  
}

h4 {
  font-family: $font-family-tertiary;
  line-height: 1;
  margin-bottom: 29px;
}

.display-1 {
  font-family: $font-family-tertiary;
  font-weight: 400;
  line-height: 0.8;
  sup {
    font-size: 20%;
    top: -48px;
    background: $primary;
    color: #F7E8F3;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    display: inline-block;
    text-align: center;
    line-height: 22px;
  }
}

.display-2 {
  line-height: calc(90 / 84);
  font-family: $font-family-tertiary;
    small {
      font-size: rem-calc(57);
      display: block;
      color: $primary;
  }
}

@media (max-width: 1199px) {
  .display-2 {
    font-size: rem-calc(60);
  }
}

@media (max-width: 991px) {
  .display-1 {
    font-size: rem-calc(65);
  }
  .display-2 {
    font-size: rem-calc(43);
    small {
      font-size: rem-calc(30);
    }
  }
}

@media (max-width: 767px) {
  h1 {
    font-size: rem-calc(30);
  }
  h4 {
    font-size: rem-calc(24);
  }
  .display-1 {
    font-size: rem-calc(30);
    sup {
      font-size: 50%;
      width: 18px;
      height: 18px;
      line-height: 20px;
      top: -17px;
    }
  }
  .display-2 {
    font-size: rem-calc(28);
    small {
      font-size: rem-calc(22);
    }
  }
}