




/*
*wave 
*/
.wave1 {
    position: relative;
    &:before {
        content: '';
        position: absolute;
        top: -25px;
        left: 0;
        height: 42px;
        width: 100%;
        background: url('../images/wave1.png') repeat-x center center;
        background-size: 748px 42px;
        animation: wave 400s cubic-bezier( 0.36, 0.36, 0.36, 0.36) infinite;
    }
    &:after {
        content: '';
        position: absolute;
        bottom: -30px;
        left: 0;
        height: 42px;
        width: 100%;
        background: url('../images/wave2.png') repeat-x center center;
        background-size: 787px 35px;
        animation: wave-reverse 400s cubic-bezier( 0.36, 0.36, 0.36, 0.36) infinite;
    }
}

