/*
*table
*/
.custom-table1 {
    td {
        font-size: rem-calc(22);
        line-height: calc(30 / 22);
        color: #8D8D8D;
        font-weight: 400;
    }
    .week {
        font-weight: 300;
        min-width: 75px;
    }
}

@media (max-width: 1199px) {
    .custom-table1 {
        td {
            font-size: rem-calc(18);
            line-height: calc(26 / 18);
        }
    }
}