/*
*section
*/
.section {
    padding: 50px 0;
    &.relative {
      .container {
        position: relative;
        z-index: 10;
      }
    }
    .cloud1 {
      top: 50%;
      //left: 48px;
      animation-delay: -160.69230769s;
      animation-duration: 88s;  
      background-size: 137px 84px;
    }
    .cloud2 {
        bottom: 50px;
        height: 60px;
        //left: 102px;
        animation-delay: -158.69230769s;
        animation-duration: 88s;
        background-size: 99px 60px;
    }
    .cloud3 {
        top: 124px;
        height: 113px;
        animation-delay: -18.46153846s;
        animation-duration: 210s;
        background-size: 186px 113px;
    }
    .cloud4 {
       top: 50%;
        height: 60px;
        //right: 214px;
        animation-delay: -22.46153846s;
        animation-duration: 200s;
        background-size: 99px 60px;
    }
    overflow-x: clip;
}

.column {
    margin-bottom: 30px;
}


/*
*stick 
*/
.stick-side {
  position: sticky;
  top: 10px;
}

/*
*sign up
*/

.sign-up-section {
    position: relative;
    &:before {
        content: '';
        position: absolute;
        width: 100%;
        height: 47.78px;
        background: url('../images/sign-up-before.png');
        background-repeat: repeat-x;
        background-size: 1460px 47.78px;
        top: -17px;
        left: 0;
        animation: wave 200s cubic-bezier( 0.36, 0.36, 0.36, 0.36) infinite;
    }
}

/*
*find us
*/
.find-us {
  z-index: 10;

  .container-fluid {
    position: relative;
    z-index: 11;
    overflow: hidden;
  }
  h2 {
    white-space: nowrap;
    .cloud1 {
      display: inline-block;
      width: 73px;
      height: 45px;
      background: url('../images/cloud2.svg') no-repeat;
      background-size: contain;
    }
  }
}

@media (max-width: 767px) {
  .find-us {
    padding: 25px 0;
    h2 {
      .cloud1 {
        width: 30px;
        height: 19px;
      }
    }
  }
}

/*
*our story
*/
.our-stores {
  padding: 80px 0 50px;
}

/*
*enquiry
*/
 .enquiry {
  padding: 85px 0 70px;
  h2 {
    font-family: $font-family-tertiary;
  }
}

@media (max-width: 767px) {
  .enquiry {
    padding: 50px 0;
  }
}

/*
*we offer
*/
.we-offer {
      position: relative;
      padding: 79px 0 105px;
      &:before {
          content: '';
          position: absolute;
          width: 100%;
          height: 47.78px;
          background: url('../images/we-offer-before.png');
          background-repeat: repeat-x;
          background-size: 752px 64px;
          top: -23px;
          left: 0;
          animation: wave 200s cubic-bezier( 0.36, 0.36, 0.36, 0.36) infinite;
      }
  h2 {
    font-family: $font-family-tertiary;
    color: #F7E8F3;
    font-weight: 400;
  }
}

@media (max-width: 767px) {
  .we-offer {
    padding: 50px 0;
  }
}

/*
*gifting-ideas
*/
.gifting-ideas {
  padding-bottom: 20px;
}

.gifting-ideas h2 {
  font-family: "Vodka Sans", sans-serif, Arial;
}

.gifting-ideas .row1 .image {
  margin-bottom: 31px;
}

.gifting-ideas .row1 h4 {
  padding: 0 20px;
}

@media (max-width: 1199px) {
  .gifting-ideas h4 {
    font-size: rem-calc(26);
  }
}

@media (max-width: 767px) {
  .gifting-ideas {
    padding-bottom: 20px;
  }
}

@media (min-width: 1440px) {
  .gifting-ideas .row1 {
    margin-right: -63px;
  }
}

/*
*ingredients
*/
.ingredients {
  header {
    margin-bottom: 80px;
  }
  h3 {
    font-weight: 900;
    font-family: $font-family-secondary;
    text-transform: uppercase;
    color: #8A5FA1;
  }
  .column {
    display: flex;
    flex-direction: column;
    .image {
      flex: 1 0 auto;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 20px;
    }
    p {
      text-transform: uppercase;
      color: #8A5FA1;
      font-size: rem-calc(18);
      font-family: $font-family-secondary;
    }
  }
}

@media (max-width: 767px) {
  .ingredients {
    padding: 30px 0 1px;
    header {
      margin-bottom: 30px;
    }
  }
}

/*
*nutritional-info
*/
.nutritional-info {
  header {
    text-align: center;
    margin-bottom: 76px;
  }
  h3 {
    font-weight: 900;
    font-family: $font-family-secondary;
    text-transform: uppercase;
    color: #8A5FA1;
  }
}

@media (max-width: 767px) {
  .nutritional-info header {
    margin-bottom: 30px;
  }
}

/*
*single-product-section
*/
.single-product-section {
  overflow-x: clip;
  position: relative;
  .container {
    position: relative;
    z-index: 10;
  }
  .side-content {
    .logo {
      margin-bottom: 31px;
    }
    h1 {
      color: #8A5FA1;
      font-family: $font-family-secondary;
      font-feature-settings:"swsh" 1; 
      font-weight: 900;
    }
    p {
      color: #8A5FA1;
      font-size: rem-calc(28);
      line-height: calc(36 / 28);
      margin-bottom: 35px;
    }
    .flavours {
      h4 {
        font-size: rem-calc(18);
        color: #8A5FA1;
        font-family: $font-family-secondary;
        font-weight: 400;
        margin-bottom: 12px;
      }
      ul {
        padding: 0;
        list-style-type: none;
        li {
          display: inline-block;
          margin-right: 21px;
         margin-bottom: 23px;
         &:last-child {
          margin-right: 0;
         }
          a {
            display: inline-block;
            padding: 12px 24px;
            color: #8A5FA1;
            border: 2px solid #8A5FA1;
            font-size: rem-calc(15);
            font-family: $font-family-secondary;
            font-weight: 900;
            border-radius: 10px;
            box-shadow: 5px 7px 0 #8A5FA1;
            &:hover {
              background: #F6E7F2;
            }
          }
        }
        .current {
          a {
            background: #F6E7F2;
          }
        }
      }
    }
    .product-price {
       ul {
        padding: 0;
        list-style-type: none;
        li {
          display: inline-block;
          vertical-align: middle;
          margin-right: 34px;
          &:last-child {
            margin-right: 0;
          }
        }
       }
    }
  }
  .cloud {
    background: url('../images/cloud3.svg') no-repeat;
  }
  .cloud1 {
    top: 134px;
    //left: 48px;
    animation-delay: -160.69230769s;
    animation-duration: 88s;  
    background-size: 137px 84px;
}
.cloud2 {
    bottom: 50px;
    height: 60px;
    //left: 102px;
    animation-delay: -158.69230769s;
    animation-duration: 88s;
    background-size: 99px 60px;
}
.cloud3 {
    top: 124px;
    height: 113px;
    animation-delay: -18.46153846s;
    animation-duration: 210s;
    background-size: 186px 113px;
}
.cloud4 {
    bottom: 122px;
    height: 60px;
    //right: 214px;
    animation-delay: -22.46153846s;
    animation-duration: 200s;
    background-size: 99px 60px;
}
.cloud5 {
    top: 60%;
    //left: 48px;
    animation-delay: -130.69230769s;
    animation-duration: 77s;  
    background-size: 137px 84px;
}
.cloud6 {
    bottom: 100px;
    height: 60px;
    //left: 102px;
    animation-delay: -50.69230769s;
    animation-duration: 88s;
    background-size: 99px 60px;
}
.cloud7 {
    top: 50%;
    margin-top: -60px;
    height: 113px;
    animation-delay: -50.46153846s;
    animation-duration: 110s;
    background-size: 186px 113px;
}
.cloud8 {
    bottom: 22px;
    height: 60px;
    //right: 214px;
    animation-delay: -40.46153846s;
    animation-duration: 160s;
    background-size: 99px 60px;
}
}

@media (max-width: 767px) {
  .single-product-section {
    .side-content {
      p {
        font-size: rem-calc(20);
        line-height: calc(26 / 20);
      }
      .flavours {
        ul {
          li {
            margin-right: 9px;
            margin-bottom: 15px;
            a {
              padding: 8px 15px;
              font-size: rem-calc(12);
            }
          }
        }
      }
      .product-price ul li  {
        font-size: rem-calc(16);
        margin-right: 22px;
      }
    }
    .cloud {
      img {
          max-height: 50px;
      }
  }
  .cloud1 {
      top: 10px;
      height: 40px;
      background-size: 70px 40px;
  }
  .cloud2 {
      bottom: 10px;
      height: 30px;
      background-size: 50px 30px;
  }
  .cloud3 {
      top: 40px;
      height: 30px;
      background-size: 50px 30px;
  }
  .cloud4 {
      bottom: 20px;
      height: 40px;
      background-size: 70px 40px;
  }
  .cloud5 {
      height: 40px;
      background-size: 70px 40px;
      top: 50%;
  }
  .cloud6 {
      height: 30px;
      background-size: 50px 30px;
  }
  .cloud7 {
      height: 25px;
      background-size: 45px 25px;
  }
  .cloud8 {
      height: 40px;
      background-size: 70px 40px;
      top: 30%;
  }
  }
}