/*
*custom-accordion
*/
.custom-accordion {
    .custom-card {
        .custom-accordion-header {
            cursor: pointer;
            font-size: rem-calc(24);
            line-height: 1;
            font-weight: 700;
            color: #8A5FA1;
            font-family: $font-family-secondary;
            padding: 16px 30px 14px 0px;
            border-bottom: 2px solid #8A5FA1;
            margin-bottom: 26px;
            position: relative;
            &:after {
                content: '';
                width: 19px;
                height: 10px;
                background: url('../images/chevron-down.svg');
                position: absolute;
                right: 0px;
                top: 50%;
                margin-top: -5px;
                transition: all 1s cubic-bezier(0.19, 1, 0.22, 1) 0s;
            }
        }
        .custom-accordion-content {
            padding: 16px 0 20px;
            display: none;
            .inner-content {
                max-width: 670px;
                margin-left: auto;
                margin-right: auto;
            }
        }
        &.active {
            .custom-accordion-header {
                &:after {
                    transform: rotate(-180deg);
                }
            }
        }
    }
}

@media (max-width: 767px) {
    .custom-accordion {
        .custom-card {
            .custom-accordion-header {
                font-size: rem-calc(20);
                margin-bottom: 14px;
            }
            .custom-accordion-content {
                padding: 10px 0 10px;
            }
        }
    }
}